 :root {
  --menu-color: rgb(255, 255, 255);
}

.App {
  padding: 10px;
  background-color: var(--menu-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgb(0, 0, 0);
}

.app-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}

.user-inputs {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 20px;
}

.plots {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.noise-plot-container {
  margin-bottom: 20px;
}

